body{
	background-color: lightskyblue;
	color: red;
	text-transform: capitalize;
	height: 100vh;
	width: 100vw;
}
img{
	max-width: 100%;
	height: auto;
}
a{
	text-decoration: none;
	border: 2px solid yellow ;
	border-radius: 20px;
	background-image: linear-gradient( to bottom,orange,pink);
}

.nav-link{
	color: darkgreen;
}
@media screen and (max-width:500px) {
	img{
		height: 200px;
		width: 200px;
	}


}
